.App {
  width: 100%;
  height: auto;
  box-sizing: border-box;
  overflow-x: hidden;
}
.main-app {
  width: 100%;
  height: auto;
  box-sizing: border-box;
  overflow-x: hidden;
}

/* nav bar */
.navbar {
  width: 100%;
  height: auto;
  box-sizing: border-box;
  position: fixed;
  background-color: rgb(251, 251, 253);
  z-index: 9;
  top: 0px;
  .navbar-container {
    width: 1000px;
    height: auto;
    box-sizing: border-box;
    margin: 0 auto;
    display: flex;
    .navbar-logo {
      width: 90%;
      height: auto;
      box-sizing: border-box;
      flex: 1, 1;
      span,
      i {
        padding: 12px 0px;
        font-size: 45px;
        color: #f4364c;
      }
    }
    .navbar-links {
      width: 100%;
      height: auto;
      box-sizing: border-box;
      flex: 1, 1;
      .navbar-links-ul {
        width: 100%;
        height: auto;
        box-sizing: border-box;
        display: flex;
        li {
          flex: 1, 1;
          list-style: none;
          margin-top: 20px;
          a {
            text-decoration: none;
            font-family: "std med", "Helvetica Neue", Helvetica, Arial,
              sans-serif;
            font-size: 14px;
            color: #000;
            transition: all 100ms;
            padding: 12px 25px 12px 25px;
            border-radius: 5px;
          }
        }
        a:hover {
          background-color: #f4364c;
          color: #fff;
        }
      }
    }
    .navbar-button-links {
      width: 100%;
      height: auto;
      box-sizing: border-box;
      flex: 1, 1;
      text-align: right;
      position: relative;
      .lets-talk {
        text-decoration: none;
        padding: 11px 25px 10px 25px;
        border: 1px solid #f4364c;
        outline: none;
        border-radius: 10px;
        margin-top: 15px;
        background-color: #f4364c;
        font-family: "std med", "Helvetica Neue", Helvetica, Arial, sans-serif;
        font-size: 14px;
        color: #f4364c;
        cursor: pointer;
        transition: all 200ms;
        color: #fff;
      }
      .lets-talk:hover {
        background-color: #f4364c;
        color: #fff;
      }
      .mobile-menu-toggle {
        text-decoration: none;
        border: 0px solid #f4364c;
        outline: none;
        border-radius: 10px;
        margin-top: 15px;
        background-color: transparent;
        color: #f4364c;
        cursor: pointer;
        transition: all 200ms;
        text-align: right;

        display: none;
        span {
          font-size: 30px !important;
        }
      }
    }
  }
}
.section-width-responsive {
  width: 1000px !important;
  height: auto;
  box-sizing: border-box;
}
/* welcome section */
.product-icons {
  width: 100%;
  height: auto;
  box-sizing: border-box;
  justify-content: center;
  margin-bottom: 20vh;
  img {
    width: 90px;
    height: 70px;
    -webkit-filter: grayscale(100%); /* Safari 6.0 - 9.0 */
    filter: grayscale(100%);
    margin-right: 10px;
  }
  img:nth-child(1),
  img:nth-child(2),
  img:nth-child(3) {
    width: 70px;
    height: 60px;
  }
  img:nth-child(4),
  img:nth-child(5) {
    width: 110px;
  }
  img:nth-child(6) {
    width: 60px;
    height: 60px;
  }
}
.welcome-background {
  width: 100%;
  height: auto;
  box-sizing: border-box;
  margin: 25vh 0vh 30vh 0vh;
}
.welcome-section {
  width: 1000px;
  height: auto;
  box-sizing: border-box;
  display: flex;
  margin: 0 auto;
  .welcome-greetings {
    flex: 1, 1;
    width: 100%;
    height: auto;
    box-sizing: border-box;
    padding: 0px 15px;
    h1 {
      font-family: "std bold", "Helvetica Neue", Helvetica, Arial, sans-serif;
      font-size: 45px;
      line-height: 1.5em;
    }
    p {
      font-family: "ttl book", "Helvetica Neue", Helvetica, Arial, sans-serif;
      font-size: 14px;
      color: #00000096;
      line-height: 1.8em;
      letter-spacing: 0.5px;
      margin-top: 20px;
    }
  }

  .welcome-banner {
    flex: 1, 1;
    width: 100%;
    height: inherit;
    box-sizing: border-box;
    position: relative;
    img {
      position: absolute;
      top: -90px;
    }
  }
}
.banner-top-clip {
  padding: 7px 5px 5px 5px;
  border-radius: 20px;
  background-color: #e9f1fc;
  text-align: center;
  margin: auto;
  display: block;
  width: 70px;
  font-family: "ttl book", "Helvetica Neue", Helvetica, Arial, sans-serif;
  font-size: 11px;
  color: #333;
  letter-spacing: 0.7px;
}
.welcome-greetings-hire-me {
  text-decoration: none;
  padding: 11px 25px 10px 25px;
  border: 1px solid #f4364c;
  background-color: #f4364c;
  outline: none;
  border-radius: 10px;
  margin-top: 15px;
  font-family: "std med", "Helvetica Neue", Helvetica, Arial, sans-serif;
  font-size: 14px;
  color: #fff;
  cursor: pointer;
  transition: all 200ms;
  margin-top: 20px;
}
.welcome-greetings-hire-me:hover {
  background-color: #f4364c;
  color: #fff;
}
.m-t-100 {
  margin-top: 100px !important;
}
.m-t-50 {
  margin-top: 50px !important;
}
.m-t-middle {
  margin: auto;
  display: block;
}
/*info banner*/
.work-info-banner {
  width: 100%;
  height: auto;
  margin: auto;
  display: block;
  box-sizing: border-box;
  padding: 20px 20px 20px 20px;

  h1 {
    font-family: "std bold", "Helvetica Neue", Helvetica, Arial, sans-serif;
    font-size: 45px;
    line-height: 1.5em;
    text-align: center;
  }
  p {
    text-align: center;
    font-family: "ttl book", "Helvetica Neue", Helvetica, Arial, sans-serif;
    font-size: 13.5px;
    color: #00000096;
    line-height: 1.8em;
    letter-spacing: 0.5px;
  }
}
.work-info-banner-contain {
  height: auto;
  margin: auto;
  display: block;
  box-sizing: border-box;
  padding-top: 30px;
  .banner-list {
    display: flex;
    width: 700px;
    height: auto;
    box-sizing: border-box;
    margin: auto;
    .banner-list-f {
      flex: 1, 1;
      width: 350px;
      height: auto;
      background-color: #fff;
      border: 1px solid #7562e0;
      border-radius: 30px;
      box-sizing: border-box;
      padding: 30px;
      h2 {
        font-family: "std bold", "Helvetica Neue", Helvetica, Arial, sans-serif;
        font-size: 25.5px;
        color: #00000096;
        margin-bottom: 30px;
        text-align: center;
      }
      p {
        font-family: "ttl book", "Helvetica Neue", Helvetica, Arial, sans-serif;
        font-size: 13.5px;
        position: relative;
        margin-top: 15px;
        display: flex;
        text-align: left;
        span {
          font-size: 15px;
          margin-right: 20px;
          position: relative;
          top: 2px;
        }
      }
    }
    .banner-list-s {
      flex: 1, 1;
      width: 350px;
      height: auto;
      background-color: #7562e0;
      border: 1px solid #7562e0;
      margin-left: 20px;
      border-radius: 30px;
      box-sizing: border-box;
      padding: 30px;
      h2 {
        font-family: "std bold", "Helvetica Neue", Helvetica, Arial, sans-serif;
        font-size: 25.5px;
        color: #f4f4f4;
        margin-bottom: 30px;
        text-align: center;
      }
      p {
        font-family: "ttl book", "Helvetica Neue", Helvetica, Arial, sans-serif;
        font-size: 13.5px;
        position: relative;
        margin-top: 15px;
        display: flex;
        color: #f4f4f4;
        text-align: left;
        span {
          font-size: 15px;
          margin-right: 20px;
          position: relative;
          top: 2px;
        }
      }
    }
  }
}
.work-project-card-contain-bg {
  background-color: #16161e;
}
.border-bottom-dashed {
  border-bottom: 1px solid #dcdcdc;
}
.work-sample {
  width: 100%;
  height: auto;
  box-sizing: border-box;
  display: flex;
  flex-wrap: nowrap;
  flex-direction: row;
}

.work-freelance-img {
  width: 400px;
  height: auto;
  position: relative;
  left: 50%;
  transform: translateX(-50%);
}
.work-project-card-contain {
  width: 100%;
  height: auto;
  box-sizing: border-box;
  margin: auto;
  position: relative;
  .work-project-cards {
    width: 500px;
    height: auto;
    box-sizing: border-box;
    display: flex;
    flex-wrap: wrap;
    margin: auto;
    position: relative;
    left: 35px;
    .work-info-cards {
      width: 100%;
      height: auto;
      box-sizing: border-box;
      display: flex;
      padding: 35px 20px;
      flex: 50%;
      img {
        width: 70px;
        height: auto;
        border-radius: 10px;
      }
      i {
        font-size: 20px;
        position: relative;
        top: 15px;
      }
      .work-info-details {
        width: auto;
        box-sizing: border-box;
        padding: 0 20px;
        display: flex;
        cursor: pointer;
        .work-info-details-desc {
          display: block;
          p {
            font-family: "ttl book", "Helvetica Neue", Helvetica, Arial,
              sans-serif;
            font-size: 13.5px;
            color: #000000d9;
          }
          span {
            font-size: 12.5px;
            color: #00000075;
          }
        }

        .work-info-btn {
          width: auto;
          box-sizing: border-box;
          position: absolute;
          right: 0px;
          span {
            font-size: 20.5px;
            color: #000000d9;
            margin: 15px 10px 15px 10px;
            display: none;
          }
        }
      }
    }
  }
}

.contact-greeting-img {
  width: 100%;
  height: auto;
  position: relative;
  top: -30px !important;
  left: 90px;
}
.social-media {
  width: auto;
  display: flex;
  li {
    list-style: none;
    padding: 10px 15px;
    background-color: #7562e0;
    margin-right: 5px;
    border-radius: 5px;
    margin-top: 15px;
    a {
      font-size: 17px;
      color: #fff;
    }
  }
}
.footer-section {
  padding: 10px;
  .footer-section-links {
    position: relative;
    padding-bottom: 10px;
    // margin-bottom: 10px;
    // border-bottom: 1px solid #d2d2d7;
    p {
      text-decoration: none;
      font-family: "sf pro text", "Myriad Set Pro", "SF Pro Icons",
        "Helvetica Neue", "Helvetica", "Arial", sans-serif;
      font-size: 12px;
      color: #515154;
      line-height: 1.5em;
      a {
        text-decoration: none;
        color: #06c;
      }
    }
  }
  .footer-section-author {
    position: relative;
    p {
      text-decoration: none;
      font-family: "sf pro text", "Myriad Set Pro", "SF Pro Icons",
        "Helvetica Neue", "Helvetica", "Arial", sans-serif;
      font-size: 12px;
      color: #515154;
      line-height: 1.5em;
    }
  }
}
/* media screen */

