  }
  .welcome-section {
    h1 {
      font-size: 35px !important;
      line-height: 1.5em;
    }
    .welcome-banner {
      img {
        width: 550px;
        height: auto;
        left: -55px;
      }
    }
  }
  .contact-greeting-img {
    width: 450px !important;
    top: -20px !important;
    left: 40px !important;
  }
}
