    width: 100% !important;
  }
  .navbar-container {
    padding: 0px 20px;
    .navbar-links-ul {
      display: none !important;
    }
  }
  .lets-talk {
    display: none !important;
  }
  .mobile-menu-toggle {
    display: inline-block !important;
    text-align: right;
  }
  .welcome-background {
    margin: 20vh 0vh 0vh 0vh;
  }
  .welcome-section {
    display: block;
    h1 {
      font-size: 36px !important;
      text-align: center;
    }
    p {
      font-size: 13px !important;
      text-align: center;
    }
    .welcome-banner {
      img {
        width: 100%;
        height: auto;
        position: relative;
        left: 50%;
        transform: translateX(-50%);
        margin-top: 30px;
      }
    }
    button {
      margin: 40px auto;
      display: block;
    }
  }
  .work-info-banner {
    h1 {
      font-size: 35px !important;
    }
  }
  .work-info-banner-contain {
    .banner-list {
      width: 100%;
      display: block !important;
      .banner-list-f {
        width: 100%;
      }
      .banner-list-s {
        width: 100%;
        margin-left: 0px !important;
        margin-top: 20px;
      }
    }
  }
  .work-project-cards {
    width: 100% !important;
    padding: 0 10px 0px 20px;
    display: block !important;
    left: 0px !important;
    padding: 0rem !important;
    .work-info-cards {
      padding: 15px 0px !important;
    }
  }
  .work-info-details {
    .work-info-btn {
      display: flex !important;
      span {
        display: block !important;
        margin: 15px 10px 15px 0px !important;
      }
    }
  }
  .github-repo-title {
    text-align: center !important;
  }
  .contact-greeting-img {
    width: 450px !important;
    top: -30px !important;
    left: 50% !important;
  }
  .social-media {
    justify-content: center;
    margin: 10px auto;
  }
  .footer-section {
    padding: 20px;
    .footer-section-links {
      p {
        line-height: 2em !important;
      }
    }
    .footer-section-author {
      p {
        line-height: 2em !important;
      }
    }
  }
}

@media screen and(max-width: 520px) {
  .hide-this-text-res {
    display: none !important;
  }

  .work-info-banner {
    h1 {
      font-size: 30px !important;
    }
  }
  .contact-greeting-img {
    width: 90% !important;
  }
}
